.container {
  max-width: 100%;
  //   padding-left: 16px;
  //   padding-right: 16px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
}
// стає контейнер адаптивним з 480px
@media screen and (min-width: 480px) {
  .container {
    width: 480px;
    //  margin-left: auto;
    //  margin-right: auto;
  }
}
@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}
@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}
