:root {
  --basic-font: "Roboto", sans-serif;
  --basic-font-size: 14px;
  --white-color: #fefefe;
  --accent-dark-green: #7eb101;
  --accent-light-green: #bbf330;
  --accent-yellow: #ffe550;
  --text-color: #2c2c2c;
  --background-gradient: radial-gradient( 50% 50% at 50% 50%, #2f583f 0%, rgba(105, 105, 105, 0) 100% );
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

._lock {
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

img {
  display: block;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

button {
  cursor: pointer;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--basic-font);
  font-size: var(--basic-font-size);
  color: var(--text-color);
}

.container {
  max-width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

@media screen and (min-width: 1280px) {
  .section {
    padding-top: 100px;
    padding-bottom: 100px;
  }
}

.nav .nav-btn {
  min-width: 98px;
  color: #2c2c2c;
  background: #bbf330;
  border: none;
  border-radius: 30px;
  padding: 6px 18px;
  font-size: 13px;
  font-weight: 500;
  line-height: 20px;
}

.backdrop {
  z-index: 1000;
  width: 100%;
  height: 100%;
  background-color: rgba(94, 99, 99, .7);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(.9);
}

.modal {
  width: 100%;
  background-color: var(--accent-yellow);
  border-radius: 30px;
  padding: 32px 20px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
}

.modal__close-btn {
  width: 32px;
  height: 32px;
  background-color: #fff;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 16px;
  right: 16px;
}

.modal__close-icon {
  width: 24px;
  height: 24px;
  fill: var(--text-color);
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.modal__close-btn:hover .modal__close-icon, .modal__close-btn:focus .modal__close-icon {
  fill: var(--accent-yellow);
}

@media screen and (min-width: 480px) {
  .modal {
    width: 480px;
  }

  .modal__close-btn {
    width: 40px;
    height: 40px;
  }

  .modal__close-icon {
    width: 30px;
    height: 30px;
  }
}

@media screen and (min-width: 768px) {
  .modal {
    width: 580px;
  }

  .modal__close-btn {
    width: 45px;
    height: 45px;
  }

  .modal__close-icon {
    width: 33px;
    height: 33px;
  }
}

.modal-caption {
  color: var(--text-color);
  text-align: center;
  cursor: default;
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.20833;
}

.modal-form__field {
