}

.modal-form__field-input {
  width: 100%;
  cursor: pointer;
  color: var(--text-color);
  border: 1px solid var(--accent-dark-green);
  background: #fff;
  border-radius: 30px;
  outline: none;
  padding: 14px 12px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
  box-shadow: inset 0 2px 4px rgba(0, 0, 0, .15);
}

.modal-form__field-input::-ms-input-placeholder {
  color: #c4c4c4;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
}

.modal-form__field-input::placeholder {
  color: #c4c4c4;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
}

.modal-form__btn {
  color: var(--text-color);
  border: 2px solid var(--accent-dark-green);
  background: #fff;
  border-radius: 30px;
  margin: 0 auto;
  padding: 10px 38px;
  font-size: 13px;
  font-weight: 500;
  line-height: 1.53846;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.modal-form__btn:hover {
  background-color: #bbf330;
}

.modal-form-btn-wrapper {
  position: relative;
}

.required-hint {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  width: 165px;
  height: 54px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: -50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.visible {
  opacity: 1;
  visibility: visible;
}

.modal-form__field-input:invalid {
  border: 1px solid #ff705d;
}

.modal-form__field-input + span {
  opacity: 0;
  visibility: hidden;
  color: #ff705d;
  margin-top: 2px;
  padding-left: 16px;
  font-size: 11px;
  font-weight: 500;
  line-height: 1.36364;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
}

.modal-form__field-input:invalid + span {
  opacity: 1;
  visibility: visible;
  display: block;
}

